"use client";
import { useSearchParams } from "next/navigation";
import React, { useState, useMemo } from 'react';

const UserManagement = () => {
    const searchParams = useSearchParams();
    const searchQuery = searchParams.get("q")?.toLowerCase() || "";

    const [users] = useState([
        { id: 1, name: "Nirmal", email: "nirmal@example.com", role: "Admin" },
        { id: 2, name: "Jane Smith", email: "jane@system.com", role: "Manager" },
        { id: 3, name: "Bob Wilson", email: "bob@coffee.com", role: "User" },
    ]);

    // Filter users based on the search query from the Navbar
    const filteredUsers = useMemo(() => {
        return users.filter(user =>
            user.name.toLowerCase().includes(searchQuery) ||
            user.email.toLowerCase().includes(searchQuery)
        );
    }, [searchQuery, users]);

    return (
        <div className="p-8">
            <h1 className="text-2xl font-bold mb-6 text-white">User Registry</h1>
            <div className="overflow-x-auto bg-white/5 rounded-xl border border-white/10">
                <table className="table w-full">
                    <thead>
                        <tr className="text-amber-500">
                            <th>Name</th>
                            <th>Email</th>
                            <th>Role</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredUsers.map(user => (
                            <tr key={user.id} className="hover:bg-white/5">
                                <td>{user.name}</td>
                                <td>{user.email}</td>
                                <td><span className="badge badge-outline">{user.role}</span></td>
                            </tr>
                        ))}
                    </tbody>
                </table>
                {filteredUsers.length === 0 && (
                    <div className="p-10 text-center text-gray-500">
                        No users found matching "{searchQuery}"
                    </div>
                )}
            </div>
        </div>
    );
};